body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}body code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.App .sidebar{position:fixed;width:12%;height:100%;background-color:#000205}.App .sidebar .sidebar-main{height:100%;overflow:scroll}.App .main{position:fixed;right:0;width:calc(88% - 80px);height:100%;border-left:solid 1px rgba(241,241,241,0.760784);padding:40px;background-color:#000205;overflow:scroll;vertical-align:top;color:#f2f2f2}.hideScrollBar{-ms-overflow-style:none;scrollbar-width:none}.hideScrollBar::-webkit-scrollbar{display:none}

.MatchThumbnail{margin:8px;border-radius:34px;padding:10px;background-color:#011533;width:calc(100% - 36px);height:200px;color:#f2f2f2;font-size:0;text-align:center;position:relative;box-shadow:inset -6px -32px 57px -62px rgba(241,241,241,0.760784);cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none}.MatchThumbnail:hover{box-shadow:inset -6px -32px 80px -62px rgba(241,241,241,0.760784);transition:all 0.2s}.MatchThumbnail img{position:absolute;height:55%;margin:20px 0}.MatchThumbnail img:first-of-type{left:16px;z-index:1}.MatchThumbnail img:last-of-type{right:16px}.activeThumbnail{background-color:#011e49;box-shadow:0px 0px 10px 0px rgba(242,242,242,0.9)}.activeThumbnail:hover{box-shadow:0px 0px 5px 0px rgba(242,242,242,0.9)}.activeThumbnail:active{box-shadow:0px 0px 5px 0px rgba(242,242,242,0.2)}

.ResultThumbnail{position:absolute;bottom:0;left:0;z-index:2;width:100%}.ResultThumbnail p{margin:0 auto;width:60%;font-size:40px;font-family:"Bebas Neue",cursive}.ResultThumbnail p span{font-size:60px;margin:0 10px}

.MatchHeader{height:36%;padding:80px 20% 0 20%;font-size:0;text-align:center}.MatchHeader .logo,.MatchHeader .footage{display:inline-block;height:100%;width:400px;text-align:center}.MatchHeader .logo img,.MatchHeader .footage img{height:100%;margin:auto}.MatchHeader .footage{cursor:pointer;-webkit-user-select:none;-ms-user-select:none;user-select:none;background-color:#011533;width:120px;height:70px;border-radius:12px;text-align:center;font-size:20px;padding:0 14px;position:relative;box-shadow:inset -6px -32px 57px -62px rgba(241,241,241,0.760784),2px 2px 6px 4px rgba(242,242,242,0.3)}.MatchHeader .footage:hover{box-shadow:inset -6px -32px 57px -62px rgba(241,241,241,0.760784),2px 2px 6px 4px rgba(242,242,242,0.2)}.MatchHeader .footage:active{box-shadow:inset -6px -32px 57px -62px rgba(241,241,241,0.760784),2px 2px 6px 0px rgba(242,242,242,0.2)}.MatchHeader .footage p{display:inline-block;line-height:70px;margin:0 auto;margin-left:10px}

.FootageThumbnail{display:inline-block;border:solid 1px #f2f2f2;margin:4px;border-radius:12px;height:200px;text-decoration:none;color:#f2f2f2;font-size:40px;overflow:hidden;position:relative}.FootageThumbnail:first-of-type{margin-left:0}.FootageThumbnail:last-of-type{margin-right:0}.FootageThumbnail:hover .play-icon{font-size:44px;opacity:1;transition:all 0.2s}.FootageThumbnail img{width:100%}.FootageThumbnail .play-icon{position:absolute;top:50%;left:50%;-webkit-transform:translate(-50%, -50%);transform:translate(-50%, -50%);line-height:200px;opacity:0.7}

.FootageBalloon{position:absolute;left:-250px;-webkit-transform:translateY(20px);transform:translateY(20px);display:grid;grid-template-columns:200px 200px 200px;background-color:#011533;border-radius:12px;padding:10px;font-size:0}.FootageBalloon .triangle{position:absolute;left:50%;width:0;height:0;border-left:10px solid transparent;border-right:10px solid transparent;border-bottom:solid 20px #011533;-webkit-transform:translateY(-100%);transform:translateY(-100%)}.hidden{display:none}

.SquadList{width:calc(32% - 21px);display:inline-block;margin:60px 20px 100px 0}.SquadList:first-of-type{text-align:right;margin:60px 0 100px 20px}.SquadList:first-of-type .player,.SquadList:first-of-type .manager{flex-direction:row-reverse}.SquadList:first-of-type .player p,.SquadList:first-of-type .manager p{margin:0 12px 0 0}.SquadList .manager{margin-bottom:10px}.SquadList .player,.SquadList .manager{height:26px;color:#f2f2f2;font-size:18px;display:flex}.SquadList .player p,.SquadList .manager p{display:inline-block;margin:0 0 0 12px;line-height:26px}.SquadList .player .person-id,.SquadList .manager .person-id{font-family:"Bebas Neue",cursive;font-size:22px;width:30px}.SquadList .player .person-name,.SquadList .manager .person-name{font-family:"Roboto",Arial}.SquadList .player .player-icons img,.SquadList .manager .player-icons img{height:20px;margin:4px}.SquadList .player:nth-of-type(n+13),.SquadList .manager:nth-of-type(n+13){color:rgba(241,241,241,0.760784);font-size:16px}.SquadList .player:nth-of-type(n+13) .player-icons img,.SquadList .manager:nth-of-type(n+13) .player-icons img{height:16px}.SquadList .player:nth-of-type(13),.SquadList .manager:nth-of-type(13){margin-top:10px}

.ResultDisplay{display:inline-block;vertical-align:top;margin:60px 0;width:36%;height:520px;border:solid 1px #f2f2f2;border-top:none;border-bottom:none}.ResultDisplay .result{text-align:center;display:inline-block;font-family:"Bebas Neue",cursive;width:30%}.ResultDisplay .result p{font-size:110px;margin:0;line-height:100px}.ResultDisplay .result p .home-goals{margin-right:4px}.ResultDisplay .result p .away-goals{margin-left:4px}.ResultDisplay .penalties{width:100%;font-size:0}.ResultDisplay .penalties .penalties-title{font-size:18px;text-align:center;margin:10px 0;font-family:"Roboto",Arial}

.GoalScorerList{display:inline-block;vertical-align:top;width:35%;padding-bottom:10px}

.GoalScorerList:first-of-type .Goal{flex-direction:row-reverse;margin:0 0 0 12px}.GoalScorerList .Goal{display:flex;margin:0 12px 0 0;justify-content:space-between}.GoalScorerList .Goal:first-of-type p{margin-top:0}.GoalScorerList .Goal p{font-family:"Roboto",Arial;display:inline-block;margin:4px 0}.GoalScorerList .Goal .goal-minute{margin:4px 6px}.GoalScorerList .Goal .goal-minute:after{content:"'"}

.PenaltiesList{display:inline-block;width:calc(50% - 20px);text-align:right;margin-bottom:20px;vertical-align:top}.PenaltiesList:last-of-type{text-align:left;margin-left:40px}.PenaltiesList:last-of-type div{flex-direction:row-reverse}.PenaltiesList div{height:26px;color:#f2f2f2;font-size:16px;display:flex;margin:0 12px 0 0;justify-content:space-between}.PenaltiesList div p{display:inline-block;margin:0 0 0 12px;line-height:26px}.PenaltiesList div .taker{font-family:"Roboto",Arial}.PenaltiesList div .scored img{height:18px}

.ExtraInformation{text-align:center;font-family:"Roboto",Arial;border-top:solid 1px #f2f2f2;margin:0 20px 120px 20px}.ExtraInformation .stadium{font-size:20px}.ExtraInformation .league{height:100%}.ExtraInformation .league img{width:25%;margin-bottom:10px}.ExtraInformation .league p{font-size:26px;text-transform:capitalize;margin:0 auto}.ExtraInformation .season{font-size:24px;margin:0}.ExtraInformation .date{font-size:18px;margin-bottom:10px}.ExtraInformation p{margin:20px auto}

